import React, { useState, useEffect, useRef } from 'react';
import './QueryHistory.css';

const QueryHistory = ({ onSelectQuery, currentQuery }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [history, setHistory] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const dropdownRef = useRef(null);

  // Load history from localStorage on component mount
  useEffect(() => {
    loadHistory();
  }, []);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
      return () => {
        document.removeEventListener('mousedown', handleClickOutside);
      };
    }
  }, [isOpen]);

  const loadHistory = () => {
    try {
      const saved = localStorage.getItem('finops-query-history');
      if (saved) {
        const parsedHistory = JSON.parse(saved);
        setHistory(Array.isArray(parsedHistory) ? parsedHistory : []);
      } else {
        setHistory([]);
      }
    } catch (error) {
      console.error('Error loading query history:', error);
      setHistory([]);
    }
  };

  const filteredHistory = history.filter(item =>
    item.query.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleSelectQuery = (historyItem) => {
    onSelectQuery(historyItem.query, historyItem.response);
    setIsOpen(false);
    setSearchTerm('');
  };

  const handleDeleteQuery = (e, itemId) => {
    e.stopPropagation();
    const updatedHistory = history.filter(item => item.id !== itemId);
    setHistory(updatedHistory);
    localStorage.setItem('finops-query-history', JSON.stringify(updatedHistory));
  };

  const clearAllHistory = () => {
    setHistory([]);
    localStorage.removeItem('finops-query-history');
    setIsOpen(false);
  };

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    const now = new Date();
    const diffInHours = (now - date) / (1000 * 60 * 60);

    if (diffInHours < 1) {
      return 'Just now';
    } else if (diffInHours < 24) {
      return `${Math.floor(diffInHours)}h ago`;
    } else if (diffInHours < 168) { // 7 days
      return `${Math.floor(diffInHours / 24)}d ago`;
    } else {
      return date.toLocaleDateString();
    }
  };

  // For debugging - let's always show the component for now
  // if (history.length === 0) {
  //   return null; // Don't show the component if no history
  // }

  // Debug logging
  console.log('QueryHistory render - history length:', history.length);
  console.log('QueryHistory render - history data:', history);

  return (
    <div className="query-history-container" ref={dropdownRef}>
      <button 
        className="history-trigger"
        onClick={() => setIsOpen(!isOpen)}
        type="button"
      >
        <span className="history-icon">🕒</span>
        <span className="history-text">
          {history.length === 0 ? 'No Query History Yet' : `Recent Queries (${history.length})`}
        </span>
        <span className={`dropdown-arrow ${isOpen ? 'open' : ''}`}>▼</span>
      </button>
      
      {isOpen && (
        <div className="history-panel">
          <div className="history-header">
            <input 
              className="history-search"
              placeholder="Search your queries..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              autoFocus
            />
            {history.length > 0 && (
              <button 
                className="clear-history-btn"
                onClick={clearAllHistory}
                title="Clear all history"
              >
                🗑️
              </button>
            )}
          </div>
          
          <div className="history-list">
            {filteredHistory.length === 0 ? (
              <div className="no-results">
                {searchTerm ? 'No matching queries found' : 'No query history yet'}
              </div>
            ) : (
              filteredHistory.map(item => (
                <div 
                  key={item.id}
                  className="history-item"
                  onClick={() => handleSelectQuery(item)}
                >
                  <div className="history-item-content">
                    <div className="query-preview">
                      {item.query}
                    </div>
                    <div className="query-meta">
                      <span className="query-timestamp">
                        {formatTimestamp(item.timestamp)}
                      </span>
                      {item.response && (
                        <span className="has-response-indicator" title="Response cached">
                          💾
                        </span>
                      )}
                    </div>
                  </div>
                  <button 
                    className="delete-query-btn"
                    onClick={(e) => handleDeleteQuery(e, item.id)}
                    title="Remove from history"
                  >
                    ✕
                  </button>
                </div>
              ))
            )}
          </div>
          
          {filteredHistory.length > 0 && (
            <div className="history-footer">
              <small>Click a query to reuse it • Cached responses load instantly</small>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default QueryHistory;
