// Query History Management Utility
const STORAGE_KEY = 'finops-query-history';
const MAX_HISTORY_ENTRIES = 50;

export const queryHistoryManager = {
  // Save a new query and response to history
  saveQuery: (query, response = null) => {
    try {
      const existingHistory = queryHistoryManager.getHistory();
      
      // Check if this exact query already exists (avoid duplicates)
      const existingIndex = existingHistory.findIndex(item => 
        item.query.trim().toLowerCase() === query.trim().toLowerCase()
      );
      
      const historyItem = {
        id: Date.now().toString() + Math.random().toString(36).substr(2, 9),
        query: query.trim(),
        response: response,
        timestamp: new Date().toISOString(),
        accessCount: 1
      };
      
      let updatedHistory;
      
      if (existingIndex !== -1) {
        // Update existing entry (move to top and update response if provided)
        const existingItem = existingHistory[existingIndex];
        updatedHistory = [
          {
            ...existingItem,
            response: response || existingItem.response,
            timestamp: new Date().toISOString(),
            accessCount: existingItem.accessCount + 1
          },
          ...existingHistory.filter((_, index) => index !== existingIndex)
        ];
      } else {
        // Add new entry to the beginning
        updatedHistory = [historyItem, ...existingHistory];
      }
      
      // Limit the number of entries
      if (updatedHistory.length > MAX_HISTORY_ENTRIES) {
        updatedHistory = updatedHistory.slice(0, MAX_HISTORY_ENTRIES);
      }
      
      localStorage.setItem(STORAGE_KEY, JSON.stringify(updatedHistory));
      return updatedHistory;
    } catch (error) {
      console.error('Error saving query to history:', error);
      return [];
    }
  },

  // Get all history entries
  getHistory: () => {
    try {
      const saved = localStorage.getItem(STORAGE_KEY);
      return saved ? JSON.parse(saved) : [];
    } catch (error) {
      console.error('Error loading query history:', error);
      return [];
    }
  },

  // Remove a specific query from history
  removeQuery: (queryId) => {
    try {
      const history = queryHistoryManager.getHistory();
      const updatedHistory = history.filter(item => item.id !== queryId);
      localStorage.setItem(STORAGE_KEY, JSON.stringify(updatedHistory));
      return updatedHistory;
    } catch (error) {
      console.error('Error removing query from history:', error);
      return queryHistoryManager.getHistory();
    }
  },

  // Clear all history
  clearHistory: () => {
    try {
      localStorage.removeItem(STORAGE_KEY);
      return [];
    } catch (error) {
      console.error('Error clearing query history:', error);
      return [];
    }
  },

  // Get recent queries (last N entries)
  getRecentQueries: (limit = 10) => {
    const history = queryHistoryManager.getHistory();
    return history.slice(0, limit);
  },

  // Get most frequently accessed queries
  getFrequentQueries: (limit = 5) => {
    const history = queryHistoryManager.getHistory();
    return history
      .filter(item => item.accessCount > 1)
      .sort((a, b) => b.accessCount - a.accessCount)
      .slice(0, limit);
  },

  // Search history by query text
  searchHistory: (searchTerm) => {
    const history = queryHistoryManager.getHistory();
    const term = searchTerm.toLowerCase().trim();
    
    if (!term) return history;
    
    return history.filter(item =>
      item.query.toLowerCase().includes(term)
    );
  },

  // Get statistics about history usage
  getHistoryStats: () => {
    const history = queryHistoryManager.getHistory();
    return {
      totalQueries: history.length,
      queriesWithResponses: history.filter(item => item.response).length,
      mostRecentQuery: history.length > 0 ? history[0] : null,
      oldestQuery: history.length > 0 ? history[history.length - 1] : null,
      averageAccessCount: history.length > 0 
        ? history.reduce((sum, item) => sum + item.accessCount, 0) / history.length 
        : 0
    };
  }
};

export default queryHistoryManager;
